.container {
  width: 350px;
  height: 100%;
  background: #56b68a;
  padding: 0 10px; }

.divider {
  height: 1px;
  background: #4f4f4f;
  border: none;
  opacity: 0.3; }

.aboutMe {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.title {
  color: #f1fffd; }

.logo {
  width: 130px;
  height: 130px;
  display: block;
  background-image: url("/static/img/puppy.jpeg");
  background-size: cover;
  background-position: 50% 50%;
  border-radius: 50%; }

.description {
  color: #f1fffd;
  word-break: break-word;
  font-size: 12px;
  letter-spacing: 0.5px; }

.sitemap a {
  text-decoration: none; }

.sitemap i {
  display: inline-block;
  transform: scale(1.6);
  margin: 0 10px; }

.category li {
  list-style: none;
  color: #f1fffd; }

.category i {
  display: inline-block; }
